.SideNavbarcontainer
{
    margin: 0px;
    display: flex;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 15%;
    z-index: 2;
    width: 10%;
    float: left;
    color: #ffffff;
    background: #0c002a;



}

.SideNavbaricons {
    align-items: center;
    float:left;
}

.SideNavbaricons ul {
    display: inline-block;
}



